@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.node {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  width: 30px;
  height: 30px;
  border: 1px solid #000;
  border-radius: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: move;
  z-index: 9999;
}

.binary-tree {
  height: 400px;
  position: relative;
  width: 1200px;
}

.svg-container {
  border-top: 2px solid rgb(0, 77, 128);
  z-index: 1;
}

.form-input {
  padding: 5px 10px;
  border: 2px solid rgb(0, 77, 128);
  border-radius: 10px;
  margin-right: 15px;
  outline: none;
  width: 210px;
}

.btn-show {
  padding: 5px 10px;
  border: 2px solid rgb(0, 77, 128);
  border-radius: 10px;
  cursor: pointer;
  outline: none;
  color: #fff;
  background: rgb(2, 113, 187);
  transition: 0.2s ease-in;
  margin-right: 15px;
}

.btn-show:hover {
  background: rgb(0, 77, 128);
}

.btn-show:active {
  background: rgb(1, 50, 83);
  border: 2px solid rgb(1, 50, 83);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.control-panel {
  margin: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  padding: 15px 15px;
  border: 2px solid rgb(0, 77, 128);
  border-radius: 10px;
}

.info-panel {
  padding: 15px 30px;
  position: relative;
}

